const { UTMs, COOKIES } = require("../values");
const { getCookie, setCookie } = require("./cookiesServices");
const { cleanUrl } = require("./functions");
const getTrafficSource = require("./getTrafficSources");
const { SENTRY_WATCH_FILES } = require("../values");
const Sentry = require("@sentry/browser");

// the first time the user lands on the page we collect the data and we add cookies
// if the cookie is set this function will not be executed again
function initUser() {
	try {
		if (!getCookie(COOKIES.CHECK)) {
			setCookie(COOKIES.CHECK, "1");

			// set the Refferer - where the user came from
			if (document.referrer)
				setCookie(COOKIES.REFERRER, cleanUrl(document.referrer));

			// set the traffic source - type of traffic - free, paid,organic etc
			let source = getTrafficSource(document.referrer, window.location.href);
			if (source) setCookie(COOKIES.SOURCE, source);

			// save the UTMs - UTMs are used to determine what AD / Email Campaign generated the user..
			const queryString = window.location.search;
			const urlParams = new URLSearchParams(queryString);
			for (const utm of UTMs) {
				if (urlParams.has(utm))
					setCookie(COOKIES.CHECK + "_" + utm, urlParams.get(utm));
			}
		}
	} catch (error) {
		throw error;
	}
}

// add snippet code of a 3rd party provider to collect visitor data
function initStats() {
	try {
		window.addEventListener("load", function () {
			// detect the Js Snippet and get the Token from data-token attribute
			let snippet = document.querySelector("#leadtrk-widget");
			if (snippet) {
				let token = snippet.getAttribute("data-token");
				if (snippet && token) {
					// create the js Snippet for 3rd party to collect visitor data
					let script = document.createElement("script");
					script.type = "text/javascript";
					script.src = `//hello.staticstuff.net/w/__stats/${token}.js`;
					// add the snippet to the header
					document.head.appendChild(script);
				}
			}
		});
	} catch (error) {
		throw error;
	}
}

module.exports = { initUser, initStats };
