const detectForms = require("./services/detectForms");
const sendAction = require("./services/sendAction");
const { initUser, initStats } = require("./services/initUser");
const initSentry = require("./services/initSentry");

// this is used for SpamBurner Websites

// initiate Sentry
initSentry();

try {
	//myUndefinedFunction11();

	// detect forms and detect on submit
	detectForms();

	// collect the initial data of the user
	initUser();

	// add Stats Snippet to the webpage to collect Data
	initStats();

	// sent the url of the page to the database to track the user history
	sendAction();
} catch (error) {
	throw error;
}

