const getLocationDetails = require("./getLocationDetails");

// check if a text has one of the items from a list
function hasItem(list, text) {
	for (let i = 0; i < list.length; i++) {
		const label = list[i].toLowerCase();
		if (text.toLowerCase().indexOf(label) > -1) {
			return true;
		}
	}
	return false;
}

// get the domain name of the given url - Issue with new Url()
function cleanUrl(url) {
	var domain = url
		.replace("http://", "")
		.replace("https://", "")
		.replace("www.", "")
		.split(/[/?#]/)[0];
	return domain[0].toUpperCase() + domain.substring(1);
}

async function addCityToSelectInForm() {
	try {
		const selectElem = document.getElementById("form-field-city");
		if (selectElem) {
			const data = await getLocationDetails();
			if (data) {
				const option = document.createElement("option");
				if (data.city !== "Not found" && data.city !== null) {
					option.text = data.city;
					option.value = data.city;
					option.selected = true;
					selectElem.add(option);
				}
			}
		}
	} catch (error) {
		console.log(error.message);
	}
}

// check if valid email
function validateEmail(emailAdress) {
	let regexEmail = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
	if (emailAdress.match(regexEmail)) {
		return true;
	} else {
		return false;
	}
}

// replace char in list of strings
function replace(name, list, replace = "") {
	for (var i = 0; i < list.length; i++) {
		name = name.replaceAll(list[i], replace);
	}
	return name.trim() !== "" ? name.trim() : "TBU";
}

// find all labels
function findLabelForControl() {
	let labels = {};
	let pageLabels = document.getElementsByTagName("label");
	if (pageLabels) {
		for (var i = 0; i < pageLabels.length; i++) {
			let label = pageLabels[i];
			let id = label.htmlFor;
			if (id) {
				labels[id] = label.textContent.trim();
			}
		}
	}
	return labels;
}

module.exports = {
	hasItem,
	cleanUrl,
	hasItem,
	addCityToSelectInForm,
	validateEmail,
	replace,
	findLabelForControl,
};
