const { BASE_URL } = require("../values");

// http requests
async function senRequest(url, data = null, method = "GET", mode = "cors") {
	try {
		let options = {
			method,
			mode,
			headers: {
				"Content-Type": "application/json",
				Accept: "application/json",
			},
		};
		if (data !== null) {
			options.body = JSON.stringify(data);
		}

		const theUrl = url.includes("http") ? url : BASE_URL + url;

		const response = await fetch(theUrl, options);
		// return await response.json();
		return response;
	} catch (error) {
		throw error;
	}
}

module.exports = senRequest;
