const { excludeUrls, SEND_LEAD, SENT_EXPIRE } = require("../values");
const { getCookie, setCookie } = require("./cookiesServices");
const dataToSubmit = require("./dataToSubmit");
const { hasItem } = require("./functions");
const senRequest = require("./sendRequest");

function detectForms() {
	// detect the list of forms on a page
	let forms = document.querySelectorAll("form");
	if (forms) {
		// setUp onsubmit event listener to all forms found
		for (let i = 0; i < forms.length; i++) {
			const form = forms[i];
			form.addEventListener(
				"submit",
				async (e) => {
					// stop form from reloading the page if is our test website
					const domain = window.location.hostname;
					if (domain === "127.0.0.1" || domain === "alphabrands.io")
						e.preventDefault();

					// prevent sending duplicated data - cookies is expiring in x seconds (Update in values)
					if (!getCookie(SEND_LEAD)) {
						// check the methode of the form only send data if is POST - to avoid capturing Search requests etc
						// check the url of the page - prevent capturing data from login, cart, checkout... etc
						if (
							form.method === "post" &&
							!hasItem(excludeUrls, window.location.href)
						) {
							try {
								// get the data from the form
								let data = await dataToSubmit(form);
								console.log(data);
								// submit the data
								const res = await submit(data);
								//console.log(res);
								// set up the cookies to prevent duplicate
								setCookie(SEND_LEAD, "1", SENT_EXPIRE);
							} catch (error) {
								throw error;
							}
						}
					} else {
						// avoid duplicate - wait until the cookies is expired
						console.log("wait...");
					}
				},
				false
			);
		}
	}
}

// submit the data to endpoint
async function submit(data) {
	try {
		return await senRequest("leads", data, "POST");
	} catch (error) {
		throw error;
	}
}
module.exports = detectForms;
