const { GEO_URL } = require("../values");

// get the user location details and ip
async function getLocationDetails() {
	try {
		let options = {
			method: "GET",
			mode: "no-cors",
			headers: {
				"Content-Type": "application/json",
				Accept: "application/json",
			},
		};
		const response = await fetch(GEO_URL, options);
		return await response.json();
	} catch (error) {
		return null;
	}
}

module.exports = getLocationDetails;
