const Sentry = require("@sentry/browser");
const { BrowserTracing } = require("@sentry/tracing");
const { SENTRY_WATCH_FILES } = require("../values");

function initSentry() {
	Sentry.init({
		dsn: "https://b0fc913ac8044124b7a865e4190f62f6@o1156580.ingest.sentry.io/6237994",
		integrations: [new BrowserTracing()],
		// Set tracesSampleRate to 1.0 to capture 100%
		// of transactions for performance monitoring.
		// We recommend adjusting this value in production
		tracesSampleRate: 1.0,
		beforeSend(event, hint) {
			const error = hint.originalException;
			// match script name, we can also match the cdn address and look for that
			if (error && error.stack &&
				(error.stack.match(SENTRY_WATCH_FILES[0]) || error.stack.match(SENTRY_WATCH_FILES[1]))) {
				console.log(event);
				return event;
			}
			if (error && error.match && (error.match(SENTRY_WATCH_FILES[0]) || error.match(SENTRY_WATCH_FILES[1]))) {
				console.log(event);
				return event;
			}
			// don't post the message 
			return null;
		},
		// allowUrls: [
		// 	// Chrome extensions
		// 	/forms/ / i,
		// 	/127\.0\.0\.1:5501\//i,
		// ],
	});
}

module.exports = initSentry;
