const { SEND_LEAD, SENT_EXPIRE } = require("../values");
const { setCookie, getCookie } = require("./cookiesServices");
const getLocationDetails = require("./getLocationDetails");
const senRequest = require("./sendRequest");

// detect if the 3rd stats party created a unique identifier for the user and send it as action on each page the user visits
const sendAction = async () => {
	window.addEventListener("load", function () {
		try {
			// wait 1 second befor sending the action - this to allow the cookie of clicky to be saved
			setTimeout(() => {
				const uid = getCookie("_jsuid") ? getCookie("_jsuid") : false;
				if (uid) {
					const data = { url: window.location.href, uid: uid };
					senRequest("website-metrics/actions", data, "POST");
				} else {
					setCookie("_jsuid", "trk_" + new Date().getTime());
				}
			}, 1000);
		} catch (error) {
			throw error;
		}
	});
};
module.exports = sendAction;
