// get the type of the browser the user is using
function identifyBrowser(userAgent, elements) {
	var regexps = {
			Chrome: [/Chrome\/(\S+)/],
			Firefox: [/Firefox\/(\S+)/],
			MSIE: [/MSIE (\S+);/],
			Opera: [
				/Opera\/.*?Version\/(\S+)/ /* Opera 10 */,
				/Opera\/(\S+)/ /* Opera 9 and older */,
			],
		},
		re,
		m,
		browser,
		version;
	if (userAgent === undefined) userAgent = navigator.userAgent;
	if (elements === undefined) elements = 2;
	else if (elements === 0) elements = 1337;

	for (browser in regexps)
		while ((re = regexps[browser].shift()))
			if ((m = userAgent.match(re))) {
				version = m[1].match(
					new RegExp("[^.]+(?:.[^.]+){0," + --elements + "}")
				)[0];
				return browser + " " + version;
			}
	return null;
}

// get the OS the user is using
function getOS() {
	var userAgent = window.navigator.userAgent,
		platform = window.navigator.platform,
		macosPlatforms = ["Macintosh", "MacIntel", "MacPPC", "Mac68K"],
		windowsPlatforms = ["Win32", "Win64", "Windows", "WinCE"],
		iosPlatforms = ["iPhone", "iPad", "iPod"],
		os = null;
	if (macosPlatforms.indexOf(platform) !== -1) {
		os = "Mac OS";
	} else if (iosPlatforms.indexOf(platform) !== -1) {
		os = "iOS";
	} else if (windowsPlatforms.indexOf(platform) !== -1) {
		os = "Windows";
	} else if (/Android/.test(userAgent)) {
		os = "Android";
	} else if (!os && /Linux/.test(platform)) {
		os = "Linux";
	}
	return os;
}

function UpFirst(string) {
	return string.charAt(0).toUpperCase() + string.slice(1);
}
// get the user Device Type
function deviceType() {
	const ua = navigator.userAgent;
	if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) {
		return "tablet";
	} else if (
		/Mobile|Android|iP(hone|od)|IEMobile|BlackBerry|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(
			ua
		)
	) {
		return "mobile";
	}
	return "desktop";
}

//all data in one object
function getUserData() {
	let device = deviceType();
	let browser = identifyBrowser();
	let userData = {
		operating_system: getOS(),
		screen_resolution: window.screen.width + "x" + window.screen.height,
		web_browser: browser + " " + UpFirst(device),
		device: UpFirst(device),
	};
	return userData;
}

module.exports = getUserData;
